import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import DropShadow from './views/DropShadow'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/drop-shadow',
      name: 'drop-shadow',
      component: DropShadow
    }
  ]
})
